* {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif; }

.src-components-app__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0;
  background: #673ab7;
  color: white; }
  .src-components-app__header .src-components-app__headersection {
    margin: 10px 30px; }
  .src-components-app__header .src-components-app__title {
    font-size: 23px; }

.src-components-app__main {
  display: flex;
  min-height: 90%;
  background: #f6f6f6; }
  .src-components-app__main .src-components-app__diarysection {
    width: 400px;
    margin: 20px auto;
    padding: 5px;
    border-radius: 3px;
    background: white;
    /* border: solid 1px lightgrey; */
    box-shadow: grey 0 0 4px 0px; }
    .src-components-app__main .src-components-app__diarysection .src-components-app__diarytitle {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 10px 20px; }
      .src-components-app__main .src-components-app__diarysection .src-components-app__diarytitle h2 {
        font-size: 20px;
        color: #673ab7; }
      .src-components-app__main .src-components-app__diarysection .src-components-app__diarytitle .src-components-app__openadd {
