          font-size: 15px; }
        .src-components-app__main .src-components-app__diarysection .src-components-app__diaryentries .src-components-app__diaryeach input {
          margin: 5px 0;
          padding: 5px;
          border-radius: 3px;
          border: solid 0.5px grey; }
        .src-components-app__main .src-components-app__diarysection .src-components-app__diaryentries .src-components-app__diaryeach .src-components-app__addbutton {
          margin: 5px 0;
          padding: 5px;
          border-radius: 3px;
